footer {
	width: 100%;
	height: 120px;

	> .inner {
		width: 1180px;
		margin: 0 auto;
		position: relative;

		> .txt {
			margin-bottom: 20px;

			li {
				display: inline-block;
				margin-right: 10px;
				font: 18px/1 'Bebas Neue';
				color: #222;
				letter-spacing: 0.5px;
				cursor: pointer;

				&:hover {
					color: #6fd885;
				}
			}
		}

		.search {
			position: absolute;
			right: 0;
			top: 0;
			width: 160px;
			border: 1px solid #eee;
			background: #fff;

			button {
				width: 30px;
				height: 20px;
				font-size: 12px;
				text-align: center;
				line-height: 20px;
				color: #222;
				background: #fff;
				border: none;
				cursor: pointer;

				&:hover {
					color: #6fd885;
				}
			}
			input {
				background: #fff;
				border: none;
				width: 125px;
				height: 20px;
				font: 12px/1 'Smooch Sans';
				color: #666;
				letter-spacing: 0.5px;
				padding-left: 10px;
			}
		}

		p {
			font: 16px/1 'Smooch Sans';
			color: #222;
			padding: 20px 0 40px;
			border-top: 1px solid #888;
		}

		.util {
			position: absolute;
			right: 0;
			bottom: 40px;

			li {
				display: inline-block;
				margin-right: 10px;
				font-size: 16px;
				color: #222;
				cursor: pointer;

				&:last-child {
					margin-right: 0;
				}
				&:hover {
					color: #6fd885;
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 100%;
			padding: 0 30px;

			.search {
				right: 30px;
			}

			.util {
				right: 30px;
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	footer {
		.inner {
			.search {
				position: relative;
				left: 0;
				margin-bottom: 20px;
			}

			.util {
				top: 40px;
			}
		}
	}
}
