* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
ul,
ol,
li {
	list-style: none;
}
a {
	text-decoration: none;
}

.hidden {
	position: absolute;
	top: -9999px;
	opacity: 0;
}

.wrap::after {
	content: '';
	display: block;
	clear: both;
}

table {
	border-collapse: collapse;
}
fieldset {
	border: none;
}
